* {
  margin: 0;
  padding: 0%;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient(90deg, #330146fd 0%, rgb(236, 47, 189));
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  min-height: 100vh;
}

header {
  font-size: 2rem;
}
header, form {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

form input, form button {
padding: 0.5rem;
font-size: 2rem;
border: none;
background: white;
}

form button {
  columns: #ff6644;
  background: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

form button:hover {
  background: coral;
  color: white;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background-image: none;
}

/*Custom select*/
.select {
  margin: 1rem;
  position: relative;
  overflow: hidden;
}
select {
  color: #2904ca;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
  width: 12rem;
}
/*Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0%;
  right: 0;
  padding: 1rem;
  background: #0a07c2;
  cursor: pointer;
  pointer-events: none;
}
.todo-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.todo {
  margin: 0.5rem;
  background: white;
  font-size: 1.5rem;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 1s ease;
}
.todo-list {
  min-width: 30%;
  list-style: none;
}
.filter-todo {
  padding: 1rem;
}
.todo li {
  flex: 1;
}

.trash-btn, .complete-btn {
  background: #0520b9;
  color: white;
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
}

.complete-btn {
  background: rgb(11, 212, 162);
}

.todo-item {
  padding: 0rem 0.5rem;
}

.fa-trash, .fa-check {
  pointer-events: none;
}

.fall {
  transform: translateY(10rem) rotateZ(20deg);
  opacity: 0;
}

